*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.header{
    color: aqua;
    width: 100%;
    background-color: rgb(81, 64, 38);
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    z-index: 100;
}
.logo{
    height: 4.5rem;
    margin: 30px;
}
.links{
    font-weight:bolder;
    font-size: 1.5rem;
    margin: 25px;
    
}
a{
    margin: 15px;
    color:rgb(226, 208, 185);
    text-decoration:none;
    outline: none;
}
a:hover{
    text-decoration:underline;
    opacity: 75%;
}