.front{
    height: 600px;
    width: 400px;
    overflow: hidden;
    z-index: 12;
    /* -ms-overflow-style: none; */
    position: absolute;
}
.back{
    height: 600px;
    width: 400px;
    overflow: hidden;
    z-index: 2;
    
}
.dragonPhoto:hover{
    transition-duration: 2s;
    opacity: 0;
}
.dragonPhoto{
    transition-duration: 1.5s;
}

.kart{
    display:inline-block;
    height: 600px;
    width: 400px;
    margin: 35px;
    margin-left: 90px;
   scale: 0.9;
   margin-top: 125px;
   
}
.name{
    text-align: center;
    font-weight: bolder;
    padding-bottom: 10px;
    background-color: rgb(200, 200, 82);
    border: 3px rgb(81, 64, 38);
    border-style: dashed;
}
img{
    height: 600px;
 width: auto;
 overflow: hidden;
}
.riderName{
    position:absolute;
    border: 5px solid black;
    padding: 7px;
    font-size: 25px;
    font-weight: bolder;
    border-radius: 10%;
    background-color: rgb(155, 111, 58);
    opacity: 0.55;
    z-index: 3;
    position: 0,50px;
    text-align: center;
    width: 400px;
    margin-top: 500px;
}

